import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Paper from 'material-ui/Paper';
import Typography from 'material-ui/Typography';
import List, { ListItem, ListItemText } from 'material-ui/List';
import Divider from 'material-ui/Divider';
const styles = theme => ({
  root: theme.mixins.gutters({
    paddingTop: 16,
    paddingBottom: 16,
    marginTop: theme.spacing.unit * 3,
    marginBottom:theme.spacing.unit * 3,
    textAlign:'left',
  }),
  p:{
      padding:0,
      textIndent:20,
      lineHeight:'40px'
  }
});

function PaperSheet(props) {
  const { classes } = props;
  return (
    <div>
      <Paper className={classes.root} elevation={4}>
        <Typography variant="headline" component="h3">
         个人简介：
        </Typography>
        <Typography component="p" className={classes.p}>
       对互联网保持高度的敏感性和关注度，熟悉产品开发流程，
        有很强的产品规划、需求分析、交互设计能力，能独立承担WEB
        目的开发工作，善于沟通，贴近用户。热爱音乐，青春阳光，
        待人随和的技术男。</Typography><Typography component="p" className={classes.p}>
        熟练使用HTML及Javascript进行web前端开发。
        </Typography><Typography component="p" className={classes.p}>
        熟练使用PHP，NODEJS进行服务器端开发，具有多个独立完成前后端项目的经验，能与他人进行协调，开发统一规定的接口。也能进行模块化开发适应项目需求。
        </Typography><Typography component="p" className={classes.p}>
        熟练使用Mysql，MongoDB并满足项目的使用需求。
        </Typography><Typography component="p" className={classes.p}>
        熟练使用VUE框架、熟练使用Node+webpack进行项目开发。
        </Typography><Typography component="p" className={classes.p}>
        使用过echart.js，chart.js，具有数据分析图像化经验。同时熟练使用Bootstrap界面库以及各种动效库。
        </Typography><Typography component="p" className={classes.p}>
        大学英语六级（CET-6），良好的听说读写能力，快速浏览英语专业文件及书籍。
        </Typography><Typography component="p" className={classes.p}>
        熟练使用PS、AE、PR等视觉后期软件。
        </Typography><Typography component="p" className={classes.p}>
        经常查看Github上的模块官方文档及使用segmentfault、stackoverflow、知乎等查找项目中遇到的问题。
        </Typography><Typography component="p" className={classes.p}>
        熟练使用ES6。
        </Typography>
      </Paper>
    </div>
  );
}

PaperSheet.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(PaperSheet);


